import * as React from 'react';
import './App.css';
import { Engine, CryptoCurrency, buyCrypto, sellCrypto } from './engine';
import * as chart from 'chart.js'
import * as rch from 'recharts'
import GraphComp from './Chart'


class CoinChoice extends React.Component<{engine: Engine, index: number, key: number},{}>{

  buy=()=>{
    this.props.engine.cryptoFunctions.buy(this.props.engine,this.props.engine.currencies[this.props.index])
  }

  sell=()=>{
    this.props.engine.cryptoFunctions.sell(this.props.engine,this.props.engine.currencies[this.props.index])

  }

  render(){
    let coin = this.props.engine.currencies[this.props.index];

    return (
      <div>
        {coin.name} - {coin.price} USD
      <button onClick={this.buy}>
        buy
      </button>
      <button onClick={this.sell}>
        sell
      </button>
      <br/>
      </div>
    )
  }
}

class CoinDropDown extends React.Component<{engine: Engine},{}>{

  render(){
    let coins = this.props.engine.currencies;
    let Coins = coins.map((coin, index: number)=>{
      return (
        <CoinChoice engine={this.props.engine} index={index} key={index} />
      )
    })

    return (
      <div>{Coins}</div>
    )
  }
}


class App extends React.Component < { } , { e: Engine } > {

  constructor(props: any){
    super(props)
    let e = new Engine();
    e.setNotify(this.notify)
    this.state = {e}
    e.start();
  }

  notify =()=>{
    //console.log('notify');
    
    let e = this.state.e
    this.setState({e})
  }

  render() {
    let engine = this.state.e;


    return (
      <div className="App">
        {engine.datamap.ticks}
        <button onClick={engine.addCurrency}>get coins</button>
        <button onClick={engine.pause}>pause</button>
        <button onClick={engine.resume}>resume</button>
        <button onClick={engine.reset}>reset</button>
        {engine.datamap.usd}
        <CoinDropDown engine={engine}/>
        <div >
          <GraphComp engine={engine}/>
        </div>
        </div>
    );
  }
}

export default App;



