export function getRandomInt(min, max) { //inclusive of max
    return Math.floor(Math.random() * (max - min + 1)) + min; 
}

export function letterNotation2(value: number): string{
    var newValue = value;
    let finalValue = ""
    let shortNum = ""
    if (value >= 1000) {
        var suffixes = ["", "k", "m", "b","t"];
        var suffixNum = Math.floor( (""+value).length/3 );
        let shortValue = 0;
        for (var precision = 2; precision >= 1; precision--) {
            shortValue = parseFloat( (suffixNum != 0 ? (value / Math.pow(1000,suffixNum) ) : value).toPrecision(precision));
            var dotLessShortValue = (shortValue + '').replace(/[^a-zA-Z 0-9]+/g,'');
            if (dotLessShortValue.length <= 2) { break; }
        }
        if (shortValue % 1 != 0) { shortNum = shortValue.toFixed(1); }
        finalValue = shortValue.toString()+suffixes[suffixNum];
    }
    return finalValue + shortNum;
}

export function letterNotation(value: number): string{
    let digits = value.toString().length;
    let mod3 = Math.floor((digits - 1 ) / 3);
    console.log(`mod3 ${mod3}`)
    let newValue = Math.floor(value / (10 ** (mod3* 3)));
    
    let suffixes = ["", `a`, `b`, `c`];
    let suffix = suffixes[mod3] 

    return newValue.toString() + suffix;
}

export function shuffle(array) {
    var currentIndex = array.length, temporaryValue, randomIndex;
  
    // While there remain elements to shuffle...
    while (0 !== currentIndex) {
  
      // Pick a remaining element...
      randomIndex = Math.floor(Math.random() * currentIndex);
      currentIndex -= 1;
  
      // And swap it with the current element.
      temporaryValue = array[currentIndex];
      array[currentIndex] = array[randomIndex];
      array[randomIndex] = temporaryValue;
    }
  
    return array;
  }


export function httpGetAsync(theUrl, callback)
{
    var xmlHttp = new XMLHttpRequest();
    xmlHttp.onreadystatechange = function() { 
        if (xmlHttp.readyState == 4 && xmlHttp.status == 200)
            callback(xmlHttp.responseText);
    }
    xmlHttp.open("GET", theUrl, true); // true for asynchronous 
    xmlHttp.send(null);
}


