import * as React from 'react';
import './App.css';
import { Engine, CryptoCurrency, buyCrypto, sellCrypto } from './engine';
import * as chart from 'chart.js'
import * as rch from 'recharts'
import * as V from 'victory'


class GraphComp extends React.Component<{engine: Engine},{}>{


  render(){

    let engine = this.props.engine;

    let chartData: number[][] = []
    let names: string[] = []
    engine.currencies.forEach((element, index) => {
        chartData.push(element.history)
    });
    

    return (
        <div className="App-graph" >
        <V.VictoryChart
            theme={V.VictoryTheme.material}
            width={900}
            height={450}
            
            containerComponent={<V.VictoryContainer width={900} height={450}/>}
            
        >
            <V.VictoryLine
                style={{ data: { stroke: "blue" }, parent: { border: "1px solid #ccc"} }}
                data={chartData[0]}
            />
            <V.VictoryLine
                style={{ data: { stroke: "red" }, parent: { border: "1px solid #ccc"} }}
                data={chartData[1]}
            />
            <V.VictoryLine
                style={{ data: { stroke: "green" }, parent: { border: "1px solid #ccc"} }}
                data={chartData[2]}
            />
            <V.VictoryLine
                style={{ data: { stroke: "yellow" }, parent: { border: "1px solid #ccc"} }}
                data={chartData[3]}
            />
            <V.VictoryLine
                style={{ data: { stroke: "purple" }, parent: { border: "1px solid #ccc"} }}
                data={chartData[4]}
            />
        </V.VictoryChart>
        </div>

    )
  }
}

export default GraphComp;


