

const datamap: DataMap  = {
    ticks: 0,
    usd: 10,
};

export interface DataMap {
    ticks: number;
    usd: number;
}

export function getMap(): DataMap{
    return datamap;
}

export function doReset(oldState: DataMap){
    let count = oldState.usd;
    oldState = datamap
    oldState.usd = 10+Math.floor(count/10)
    
}


