import * as workerPath from 'file-loader?name=[name].js!./test.worker';
import {DataMap, getMap, doReset} from './index';
import { httpGetAsync } from './util';
import { CryptoCurrency } from './crypto';
import * as crypto from './crypto';

class CoreEngine {

    public datamap: DataMap = getMap();
    worker: Worker; 
    notify: VoidFunction;
    setNotify(notify: VoidFunction){
        this.notify = notify;
    }
    
    start= () => {
        this.worker = new Worker(workerPath);
        this.worker.postMessage('hello world');   
        this.worker.onmessage = this.tick;
    }

    pause=()=>{
        this.worker.onmessage = this.dontTick;
    }
    resume=()=>{
        this.worker.onmessage = this.tick;
    }

    dontTick= () => {
        console.log('donttick');
       
        //this.notify();
    }

    tick= () => {
        console.log('tick');
       
        this.notify();
    }

    save= () => {
        console.log('saving');
        let datamap = JSON.stringify(this.datamap);
        
        localStorage.setItem('datamap', datamap);
        console.log(datamap);
        this.notify();
    }

    load= () => {
        console.log('loading');
        
        let combstr = localStorage.getItem('datamap');
        if (combstr != null){
            let oldobj = JSON.parse(combstr);
            this.datamap = oldobj
        }
        this.notify();
    }

    reset = () => {
        //let newXp, newLevel = this.getNewResetXP();
        //_.merge(this.datamap, getMap())
        doReset(this.datamap);
        this.notify();
    }

}

export class Engine extends CoreEngine {
    
        currencies: CryptoCurrency[] = [];

        setupCurrencies=()=>{
            this.currencies = [];
            for (let x = 0; x < 5; x++){
                this.currencies.push( new CryptoCurrency(this.currencies.length) );                
            }
        }

        constructor(){
            super()
            this.setupCurrencies();
        }

        tick = () =>{
            console.log(this.datamap.ticks);
            
            this.datamap.ticks++;
            this.currencies.forEach(curren => {
                curren.tick();
            });
            this.notify();
        }

        addCurrency = () => {
            this.currencies.push( new CryptoCurrency(this.currencies.length) );
            this.notify();
        }

        cryptoFunctions = {
            buy: crypto.buyCrypto,
            sell: crypto. sellCrypto,
        }

        reset = () => {
            //let newXp, newLevel = this.getNewResetXP();
            //_.merge(this.datamap, getMap())
            doReset(this.datamap);
            this.setupCurrencies();
            this.notify();
        }
}




