import { getRandomInt } from './util';
import { Engine } from './index';

const prenames = [
    'bit','ether','doge', 'dash', 'investo', 'gold', 'denta'
]

const postnames = [
    'cash','coin', 'dolla', 'val', 'bux', 'coinz', 'boom'
]

export class CryptoCurrency {

    id: number;
    name: string;
    price: number;
    age: number = 0;
    count: number = 1000000;
    owned: number = 0;
    private magic: number;

    history: number[] = []

    constructor(id: number){
        this.id = id;
        let pre = prenames[getRandomInt(0, prenames.length-1)];
        let post = postnames[getRandomInt(0, postnames.length-1)];
        this.name = pre+post;
        this.price = getRandomInt(1, 10);
        this.magic = getRandomInt(0, 100)
    }

    tick=()=>{
        if (this.price <= 0) return;
        this.history.push(this.price)
        this.age ++;
        let phaselength = 60 + this.magic;
        let phase = Math.floor( this.age / phaselength )
        let direction = 1 - 2 * ( phase % 2 )
        let rng = 1//getRandomInt(-10,10)
        

        if (phase === 0){
            rng = rng + 5
        } else if (phase === 1 ) {
            
        } else if (direction === 1){
            rng = rng + 5
        } else if (direction === -1){
            rng = rng - 5 - phase
        } else {
            console.log('ERROR!!! - crypto');
        }

        if (phase >= 1){
            rng = rng * phase
            rng = rng + getRandomInt(-10,10)
        }

        let mult = rng / 100;
        let change = mult * this.price;

        if (phase === 0){
            this.price = Math.max(1, this.price + change)
        } else {
            this.price = this.price + change;
        }

        this.price = Math.floor(this.price * 1000 ) / 1000
    }

    
}

export function buyCrypto (engine: Engine, crypto: CryptoCurrency) {
    if (engine.datamap.usd >= crypto.price) {
        engine.datamap.usd -= crypto.price
        crypto.owned ++;
        crypto.count --;
        engine.notify()
    } else {
        return;
    }
}

export function sellCrypto (engine: Engine, crypto: CryptoCurrency) {
    if (crypto.owned > 0) {
        engine.datamap.usd += crypto.price
        crypto.owned --;
        crypto.count ++;
        engine.notify()
    } else {
        return;
    }
}

